body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

html, body, #App {
  height: 100%;
  overflow: scroll;
}

body{
  width: 100vw;
  background-color: rgb(68, 106, 116);
}

main{
  background-color: rgb(41, 65, 65);
}

.navbar{
  width: 100%;
  height: 50px;
  background-color: rgb(0, 0, 0);
  padding: 10px 0;
}

.navbar ul{
  display: flex;
  justify-content: flex-start;
  height: 100%;
  list-style: none;
  text-align: center;
}
.navbar ul li{
  padding: 5px;
}
.navbar ul li button{
  width: 100%;
  height: 100%;
  padding: 5px;
  list-style: none;
  cursor: pointer;
  color: white;
  background-color: black;
}

.xSquare{
  background-color: rgb(128, 2, 2);
}

.oSquare{
  background-color: rgb(90, 85, 85);
}

button{
  outline: none;
  border: 5px solid black;
}

.game-window{
  height: calc(100% - 120px);
}

/***********************************************************************

  TIC TAC TOE

***********************************************************************/
.main_Tac_grid{
  text-align: center;
  width: 250px;
  height: 250px;
  margin: auto;
  padding: 100px 0;
}

.tac_game{
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  height: 100%;
}

.tic_tac_stats{
  text-align: center;
  color: white;
  padding: 10px;
}

.tic_tac_stats button{
  border: 1px solid black;
  width: 120px;
  height: 40px;
}
.ticBtns{
  cursor: pointer;
}

/*          Connect Four     */
.connect4_grid{
  text-align: center;
  width: 100%;
}
.four-sqrBoard{
  width: auto;
  height: 50vh;
  background-color: white;
  display: grid;
  grid-template-rows: auto auto auto auto auto auto;
}

.game-row{
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto;
}

.game-row button{
  cursor: pointer;
}

.row-item{

  text-align: center;
  border: 5px solid;
}

.connect_4_stats{
  text-align: center;
  color: white;
}

#chooseBtn{
  background-color: grey;
}

/*          Monty       */
.monty_grid{
  width: 100vw;
  height: 50vh;
  display: grid;
  grid-template-columns: auto auto auto;
  text-align: center;
  background-color: black;
}

.monty_door{
  width: 30vw;
  height: 90%;
  margin: 5%;
  background-color: white;
  cursor: pointer;
}

.door_img{
  height: 100%;
  width: 100%;
}

.monty_stats{
  color: white;
  text-align: center;
  display: grid;
  grid-template-rows: auto auto;
}

.monty_stats section{
  display: grid;
  grid-template-columns: auto auto;
  padding: 10px;
}

.tic_tac_stats div, .connect_4_stats div{
  padding: 10px;
}

@media only screen and (max-width: 500px){
  body{
    width: auto;
  }
}